import { returnBody } from "../utils/parse-string";
import { CALENDAR_ISLAND } from "../json/json";
import { addZero } from "utils/util";

export interface Calendar {
  name: string;
  src: string;
  lv: number;
  position: string;
  contType: string;
  time: string[];
  endTime: string;
}

interface Props {
  calendar: Calendar[][];
}

export default class HomeData implements Props {
  calendar: Calendar[][];

  constructor(calendar: string) {
    // 캘린더섬
    this.setCalendar(returnBody(calendar));
  }

  setCalendar(raw: Element) {
    const timerList: Element[] = [...raw.getElementsByClassName("info")];
    const _: Date = new Date();
    const today: string = `${_.getFullYear()}-${addZero(
      _.getMonth() + 1
    )}-${addZero(_.getDate())}`;
    this.calendar = timerList.reduce(
      (prev, next) => {
        const name: string = next.getElementsByClassName("npcname")[0]
          .textContent;
        const target: object = CALENDAR_ISLAND.find(
          (cal: object) => cal["name"] === name
        );
        if (!next.textContent.includes(today) || !target) return prev;
        if (next.textContent.includes("14:00")) {
          prev[0].push({ ...target, time: ["14:00"], endTime: "14:00" });
        } else if (next.textContent.includes("21:00")) {
          prev[1].push({ ...target, time: ["21:00"], endTime: "21:00" });
        }
        return prev;
      },
      [[], []]
    );
  }
}
